.edit-company-create{
  position: fixed;
  width: 80%;
  z-index: 120;
  background-color: white;
  padding: 20px;
  border: 1px solid #202041;
  border-radius: 10px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  @media (max-height: 530px) {
    position: relative;
    top: unset;
    left: unset;
    transform: unset;
    margin: 0 auto;
  }
  .button-div{
    display: flex;
    justify-content: end;
  }
  h3{
    text-transform: capitalize;
    margin: 0;
  }

  button{
    margin-top: 10px;
    margin-left: auto;
    padding: 14px 36px;
    border: none;
    cursor: pointer;
    color:#ffffff;
    font-size: 16px;
    border-radius: 10px;
    background: linear-gradient(to right, #13b5f3, #3684d9);
    //@media(max-width: 991px){
    //    padding: 14px;
    //
    //}

  }
  .close {
    cursor: pointer;
    font-size: 25px;
    position: absolute;
    top: 18px;
    right: 0%;
    padding: 12px 16px;
    transform: translate(0%, -50%);
  }
  form{
    .input-wrapper{
      input{
        width: 98.1%;
        font-size: 16px;
        height: 52px;
        padding: 0 10px;
        border-radius: 4px;
        border: 1px solid #dedede;
        @media(max-width: 991px){
          width: 97%;

        }
        @media(max-width: 700px){
          width: 97%;

        }
        @media(max-width: 700px){
          width: 95%;

        }
        @media(max-width: 700px){
          width: 93%;

        }
      }

    }

  }

}